import {allTags} from '../Posts/PostCard';

export const FilterBar =()=>{
    var tagArr = Array.from(allTags);
    return(
        <div>
            {tagArr && tagArr.map((tag:string)=><button onClick={filterSelection(tag)} key={tag}>{tag}</button>)}
        </div>
    )
    
}

